.seccion2 {
    background: linear-gradient(180deg, rgba(11, 108, 184, 0.3) 20%, rgba(11, 108, 184, 0.1) 60.5%, rgba(11, 108, 184, 0.3) 100%);
    position: relative;

    .backImage {
        overflow: hidden;
        width: 100%;

        img {
            width: 125%;
            height: 620px;
        }
    }

    .cardContainer {
        position: absolute;
        z-index: 10;
        width: 100%;
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: center;

        h2 {
            margin: 0px;
        }

        .cardContent {
            background-color: #ffffff;
            max-width: 820px;
            width: 90%;
            height: 420px;
            border-radius: 20px;
            padding: 40px;

            li {
                line-height: 60px;
                font-size: 1.25rem;
                color: #015AA1;
            }
        }
    }
}


@media only screen and (max-width: 1024px) {
    .seccion2 {
        .backImage {
            img {
                width: fit-content;
                height: 580px;
            }
        }
        .cardContainer {
            .cardContent {
                height: 340px;
                li {
                    line-height: 40px;
                    font-size: 1.25rem;
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {
    .seccion2 {
        .backImage {
            img {
                width: fit-content;
                height: 550px;
            }
        }
        .cardContainer {
            .cardContent {
                width: 95%;
                padding: 25px;
                li {
                    line-height: 40px;
                    font-size: 1rem;
                }
            }
        }
    }
}

@media only screen and (max-width: 425px) {
    .seccion2 {
        .backImage {
            img {
                width: fit-content;
                height: 520px;
            }
        }
        .cardContainer {
            .cardContent {
                width: 95%;
                padding: 25px;
                ul{
                    margin: 0;
                    padding-left: 20px;
                    li {
                        line-height: 30px;
                        font-size: 1rem;
                        color: #015AA1;
                    }
                }
                
            }
        }
    }
}

