.seccion3 {
    background: linear-gradient(180deg, rgba(11, 108, 184, 0.3) 5%, rgba(11, 108, 184, 0.1) 25%, rgb(255 255 255 / 70%) 100%);
    padding: 20px;
    h2{
        text-align: center;
    }
    .cardList {
        display: grid;
        width: fit-content;
        grid-template-rows: auto auto;
        grid-template-columns: auto auto;
        row-gap: 20px;
        column-gap: 40px;
        margin: 10px auto 60px auto;
        .cardContainer {
            position: relative;
            width: 450px;
            height: 240px;

            .cardOverlay {
                position: absolute;
                height: 100%;
                width: 100%;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0) 50%);
                z-index: 1;
            }

            .cardContent {
                position: relative;
                border: 4px solid #CAA403;
                border-radius: 11px;
                overflow: hidden;
                height: 100%;
                z-index: 2;
                h3 {
                    position: absolute;
                    left: 30px;
                    color: #FFFFFF;
                    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                    font-family: 'Montserrat', sans-serif;
                    font-style: normal;
                    font-weight: 700;
                    font-size: 1.5rem;
                    line-height: 20px;
                    z-index: 3;
                }
                
                img{
                    height: 100%;
                }
            }

            .puntos {
                position: absolute;
                z-index: 0;
            }
        }

        .cardContainer:nth-child(1){
            .puntos{
                left: -58px;
                top: -50px;
            }
        }
        .cardContainer:nth-child(2){
            .puntos{
                right: -58px;
                top: -50px;
            }
        }
        .cardContainer:nth-child(3){
            .puntos{
                left: -58px;
                bottom: -50px;
            }
        }
        .cardContainer:nth-child(4){
            .puntos{
                right: -58px;
                bottom: -50px;
            }
        }
    }

    .circle {
        display: flex;
        justify-content: center;
        img{
            width: 200px;
        }
    }
}

@media only screen and (max-width: 1024px) {
    .seccion3 .cardList {
        row-gap: 20px;
        column-gap: 20px;
        margin-bottom: 35px;
    }
    .seccion3 .cardList .cardContainer {
        width: 320px;
        height: 170px;

        .cardContent h3{
            font-size: 1rem;
            margin: 10px 0px;
            left: 15px;
        }
    }
}

@media only screen and (max-width: 768px) {
    .seccion3{
        padding: 0px;
        padding-top: 20px;
    }
    .seccion3 .cardList {
        grid-template-rows: auto;
        grid-template-columns: auto;
        row-gap: 20px;
        column-gap: 20px;
        margin-bottom: 35px;

        .cardContainer:nth-child(1){
            .puntos{
                left: 0px;
                top: -25px;
            }
        }
        .cardContainer:nth-child(2){
            .puntos{
                right: 0px;
                top: -25px;
            }
        }
        .cardContainer:nth-child(3){
            .puntos{
                left: 0px;
                bottom: -25px;
            }
        }
        .cardContainer:nth-child(4){
            .puntos{
                right: 0px;
                bottom: -25px;
            }
        }
    }
    .seccion3 .cardList .cardContainer {
        width: 320px;
        height: 170px;

        .cardContent h3{
            font-size: 1rem;
            margin: 10px 0px;
            left: 15px;
        }
    }

    .seccion3 .circle {
        img{
            width: 150px;
        }
    }
}