.header {
    height: 60px;
    padding: 5px 30px;
    width: 100%;
    position: fixed;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    background-color: white;
    transition: all 0.3s ease-in-out;
    z-index: 100;

    &.solid {
        background: none;
        box-shadow: none;

        span {
            color: #ffffff;
        }

        .menu li a {
            color: #ffffff;
        }
    }

    .menu {
        ul {
            display: inline-flex;
            margin: 0px;
            li {
                height: 60px;
                display: flex;
                align-items: center;
                margin: 0px 10px;
                list-style-type: none;
    
                a {
                    cursor: pointer;
                    text-decoration: none;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 74px;
                    color: #0578DD;
                }
            }
        }
        

    }

    .menubtn {
        display: none;
    }
}


@media only screen and (max-width: 768px) {
    .header {
        .menubtn {
            display: inline;
        }

        .menu {
            display: none;
            position: absolute;
            top: 0px;
            right: -100px;
            background: linear-gradient(90deg, rgba(0, 70, 126, 0) 0%, rgba(0, 79, 140, 0.63) 43.59%, #00467E 89.17%); 
            height: 100vh;
            width: 268px;
            transition: all 0.3s ease-in-out;
            transform: translate(0px);
            &.showMove{
                transform: translate(-100px);
            }
            &.show {
                display: block;
                .menubtn{
                    height: 60px;
                    display: inline-flex;
                    width: 100%;
                    justify-content: flex-end;
                    align-items: center;
                    padding-right: 30px;
                    span{
                        color: #ffffff;
                        font-size: 1.25rem;
                    }
                }
            }

            ul{
                display: flex;
                flex-direction: column;
                li {
                    list-style-type: none;
                    justify-content: flex-end;
                    margin-right: 40px;
                    a {
                        text-align: right;
                        color: #ffffff;
                    }
                }
            }

        }
    }

}
