.footer {
    background: linear-gradient(180deg, #004982 0%, #012F54 100%);
    height: 240px;
    color: #ffffff;
    padding: 40px;
    display: inline-flex;
    justify-content: space-around;
    width: 100%;

    .redes {
        .icons {
            display: inline-flex;
            font-size: 2.5rem;

            a {
                margin: 0 5px;
                color: #ffffff;
            }
        }
    }
}

@media only screen and (max-width: 425px) {
    .footer {
        padding: 20px;
        height: 200px;

        h3{
            font-size: 1rem;
        }
        .redes {
            
            .icons {
                font-size: 1.75rem;
                a {
                    margin: 0 5px;
                    color: #ffffff;
                }
            }
        }
        .contact{
            p{
                font-size: 0.75rem;
            }
        }

    }

}