.seccion4 {
    background: linear-gradient(180deg, rgb(255 255 255 / 70%) 0%, #D1EBFF 43.44%, #D1EBFF 60.1%, rgba(195, 219, 238, 0) 100%);
    position: relative;
    padding-top: 20px;
    height: 900px;

    .s4content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 75%;
    }

    h2 {
        text-align: center;
        margin-top: 35px;
        margin-bottom: 0px;
    }

    .imgBack {
        display: flex;
        justify-content: center;
        width: 100%;
        position: absolute;
        top: -50px;
        height: 100%;

        img {
            width: 100%;
            object-fit: contain;

            &.desktop {
                display: block;
            }
        }

    }

    .cardsContainer {
        width: 60%;
        margin: 0 auto;
        position: relative;

        height: 40%;
        top: 50px;

        .card:hover {
            transform: scale(1.01);
        }

        .card {
            position: absolute;
            left: 0;
            top: 50px;
            width: 60%;
            left: 20%;
            height: 320px;
            background: #FFFFFF;
            transition: all 0.3s ease-in-out;
            -webkit-tap-highlight-color: transparent;
            box-shadow: 0px 18px 20px rgba(215, 228, 249, 0.5);
            border-radius: 20px;
            z-index: 0;

            display: flex;
            flex-direction: column;
            align-items: center;

            padding: 40px;
            cursor: pointer;

            .text {
                font-size: 1.5rem;
                line-height: 40px;
                color: #69727D;
                margin-top: 20px;
                display: none;
                text-align: center;
            }

            img {
                width: 150px;
            }

            &.previous {
                transform: translate(-30px);
                opacity: 0;
                top: 0;
                left: 0;
                z-index: 3;
                width: 100%;
                height: 350px;
            }

            &.current {
                top: 0;
                left: 0;
                z-index: 3;
                width: 100%;

                .text {
                    display: inline-block;
                }

            }

            &.next {
                z-index: 2;
                top: 25px;
                width: 80%;
                left: 10%;
            }

            &.next2 {
                z-index: 1;
                top: 40px;
                width: 70%;
                left: 15%;
            }


        }
    }
}

@media only screen and (max-width: 768px) {
    .seccion4 {
        height: 680px;

        .s4content {
            display: block;
        }

        .imgBack {
            top: -100px;
            img {
                width: 100%;
            }
        }

        .cardsContainer {
            width: 90%;

            .card {
                height: 280px;

                img {
                    width: 120px;
                }

                .text {
                    font-size: 1rem;
                }

            }

        }
    }
}

@media only screen and (max-width: 425px) {
    .seccion4 {
        height: 600px;

        .imgBack {
            bottom: 0px;
            img {
                width: 100%;

                &.desktop {
                    display: none;
                }
            }
        }

        .cardsContainer {
            width: 100%;

            .card {
                height: 280px;

                img {
                    width: 120px;
                }

                .text {
                    font-size: 1rem;
                }

            }

        }
    }
}